import React, { useEffect, useRef } from 'react';
import { dropRight, times } from "lodash";
import { __ } from "@wordpress/i18n";
import {
  PanelBody,
  RangeControl,
} from "@wordpress/components";
import {
  InspectorControls,
  useInnerBlocksProps,
  useBlockProps,
  store as blockEditorStore,
} from "@wordpress/block-editor";
import { withDispatch, useSelect, subscribe } from "@wordpress/data";
import Accordion from 'accordion-js';
import { createBlock } from "@wordpress/blocks";
import './editor.scss';
import 'accordion-js/dist/accordion.min.css';

// Allowed blocks and template constants
const ALLOWED_BLOCKS = ["wptravel/accordion-item"];
const TEMPLATE = [["wptravel/accordion-item"], ["wptravel/accordion-item"]];

function AccordionEditContainer({
  attributes,
  setAttributes,
  updateColumns,
  clientId,
}) {
  const accordionRef = useRef(null); // Ref for the accordion container

  const { count, blockList } = useSelect(
    (select) => {
      const { getBlockCount, getBlocks } = select(blockEditorStore);
      return {
        count: getBlockCount(clientId),
        blockList: getBlocks(clientId), // Obtain the block list from the store
      };
    },
    [clientId]
  );

  // Subscribe to block changes
  useEffect(() => {
    const handleBlockChange = () => {
      // Using the current blockList from the hook state
      const newBlockList = blockList;
      if (newBlockList !== blockList) {
        const changeEvent = new Event("elementsChanged");
        document.dispatchEvent(changeEvent);
      }
    };

    const unsubscribe = subscribe(handleBlockChange);
    return () => unsubscribe();
  }, [blockList]);

  const blockProps = useBlockProps();
  const innerBlocksProps = useInnerBlocksProps(blockProps, {
    allowedBlocks: ALLOWED_BLOCKS,
    orientation: "horizontal",
    renderAppender: false,
    template: TEMPLATE,
  });

  // Initialize Accordion in useEffect
  useEffect(() => {
    if (accordionRef.current) {
      new Accordion(accordionRef.current);
    }
  }, [clientId]); // Dependencies array to reinitialize if clientId changes

  return (
    <>
      <InspectorControls>
        <PanelBody>
          <RangeControl
            label={__("Number of Accordion")}
            value={count}
            onChange={(value) => updateColumns(count, value)}
            min={1}
            max={Math.max(10, count)}
          />
        </PanelBody>
      </InspectorControls>
      <div {...blockProps}>
        <div ref={accordionRef} className="accordion-container">
          {innerBlocksProps.children}
        </div>
      </div>
    </>
  );
}

const AccordionEditContainerWrapper = withDispatch(
  (dispatch, ownProps, registry) => ({
    updateColumns(previousColumns, newColumns) {
      const { clientId } = ownProps;
      const { replaceInnerBlocks } = dispatch(blockEditorStore);
      const { getBlocks } = registry.select(blockEditorStore);

      let innerBlocks = getBlocks(clientId);
      const isAddingColumn = newColumns > previousColumns;

      if (isAddingColumn) {
        innerBlocks = [
          ...innerBlocks,
          ...times(newColumns - previousColumns, () => createBlock("wptravel/accordion-item")),
        ];
      } else {
        innerBlocks = dropRight(innerBlocks, previousColumns - newColumns);
      }

      replaceInnerBlocks(clientId, innerBlocks);
    },
  })
)(AccordionEditContainer);

const AccordionEdit = (props) => {
  const Component = AccordionEditContainerWrapper;
  return <Component {...props} />;
};

export default AccordionEdit;
